import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import {
	myrequest,
	listurl,
	myget
} from '../../api.js';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			headerTop: "0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0,
			goodsList: [],
			pageIndex: 0,
		};
	},

	async onLoad() {



		this.loadData(this.pageIndex + 1,this.pageIndex);
	},
	async onShow() {
		this.pageIndex = await myget('serviceIndex');
		this.loadData(this.pageIndex + 1, this.pageIndex);
		uni.setStorage({
			key: 'serviceIndex',
			data: 0,
		});
	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(typeindex, index) {
			this.filterIndex = index;
			let goodsList = await myrequest(listurl, 'GET', {
				type: typeindex
			});
			this.goodsList = goodsList["data"];
			this.loadingType = 'nomore';
		},
		//筛选点击
		tabClick(index) {
			this.loadData(index + 1, index);
		},
		//服务详情
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/service/product?id=${id}`
			})
		},
		stopPrevent() {}
	},
}
